export class TaskAddBean{
    taskCategoryName:string;//任务名称
    taskTypeParentId:string;//任务大类
    taskType:string;//任务小类
    itemsTypeParentId:string;//任务对应事项大类
    itemsTypeId:string;//任务对应事项小类   
    url:string;//任务对应URL
    statusId:string;//任务状态
    processCode:string;//任务对应流程id
    processName:string;//任务对应流程名称
    processChildCode:string;//任务对应流程子节点
    taskCategoryRel:string;//关联任务
    isSkip:string;//是否跳过
    isAutoTask:string;//是否自动任务
    subSysId:string;//所属系统
    isParentTask:string;//是否关联任务
    serviceName:string;//自动服务
    methodName:string;//任务名称
    isCallBack:string;//是否回调服务
    inputBean:string;//入参BEAN
    isShow:string;//是否在工作任务中展示
    orderNo:string;//排序
}