import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { ConfirmDialogModule, ConfirmationService, Message, SelectItem } from 'primeng/primeng';
import { TaskAddBean } from './bean/taskAdd.bean';
@Component({
    selector: 'workTaskAdd',
    templateUrl: './workTaskAdd.component.html',
    styleUrls: ['./workTaskAdd.component.scss'],
    providers: [ConfirmationService, CusOperationHttpService, Commfunc]
})
export class WorkTaskAddComponent implements OnInit {
    @Input() inValue: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter<any>();
    code: any;
    taskcodeshow: boolean = false;
    relationshow: boolean = false;
    flow: boolean = false;
    selfTaskY: boolean = false;
    selfTaskN: boolean = false;
    statusOptions: any;//工作任务状态
    yesOrNoItems: any;//是否
    itemsTypeOptions: any;//事项大小类
    workTaskTypeOptions: any;//任务大小类
    first: number = 0;//分页控制
    //总页数
    totalNum: string;
    //每页多少条
    pageSize: string;
    msgs: Message[] = [];
    applymgs: any = [
        { label: '请选择', value: '' }, { label: '是', value: '0' }, { label: '否', value: '1' },
    ];
    taskBigType: any = [];//任务大类
    itemsBigType: any = [];//事项大类
    taskSmallType: any = [];//任务小类
    itemsSmallType: any = [];//事项小类
    statusIdQry: any = [
        { label: '请选择', value: '' }, { label: '正常', value: '0' }, { label: '停用', value: '1' },
    ];
    subSysIdQry: any = [
        // { label: '请选择', value: '' },
        // { label: '客户运营平台', value: 'COP' },
        // { label: '统一计价平台', value: 'UVP' },
        // { label: '产品运营平台', value: 'POP' },
        // { label: '客户管理系统', value: 'CRM' },
    ]
    RelationTaskList: any = [];//关联任务
    taskAddBean: TaskAddBean = new TaskAddBean();
    constructor(
        public httpService: CusOperationHttpService,
        public commfunc: Commfunc,
    ) { }
    ngOnInit() {
        this.codeValues();
        this.statusOptions = this.code['StatusWorkTask'];
        this.yesOrNoItems = this.code['YesOrNo'];
        this.itemsTypeOptions = this.code['ItemsType'];
        this.workTaskTypeOptions = this.code['WorkTaskBigType'];
        this.subSysIdQry = this.code['SubSysIdQry'];

        this.taskBig();
        this.itemsBig();
    }
    //是否关联任务
    changethis() {
        if (this.taskAddBean.isParentTask == '0') {
            this.relationshow = true;
            this.flow = false;
            this.taskcodeshow = true;
            this.httpService.taskCategoryListQry({}).subscribe(data => {
                data.taskCategoryList.forEach((item, i) => {
                    this.RelationTaskList.push({ label: item.taskCategoryName, value: item.taskCategoryId })
                });
            })
        } else if (this.taskAddBean.isParentTask == '1') {
            this.relationshow = false;
            this.flow = true;
            this.taskcodeshow = true;
        } else {
            this.taskcodeshow = false;
            this.flow = false;
            this.relationshow = false;
        }
    }
    //任务大类
    taskBig() {
        let item = {
            taskTypeKey: 'taskType'
        }
        this.httpService.taskTypeQry(item).subscribe(data => {
            let arr = data.taskTypeList;
            this.taskBigType = [{ label: '请选择', value: '' },];
            arr.forEach(param => {
                this.taskBigType.push({ label: param.taskTypeName, value: param.taskTypeId });
            });
        })
    }
    //任务大类切换
    taskBigChange() {
        if (this.taskAddBean.taskTypeParentId && this.taskAddBean.taskTypeParentId != '') {
            let params = {
                taskTypeParentId: this.taskAddBean.taskTypeParentId,
                taskTypeKey: 'taskType'
            }
            this.httpService.taskTypeQry(params).subscribe(data => {
                let arr = data.taskTypeList;
                this.taskSmallType = [{ label: '请选择', value: '' },];
                arr.forEach(item => {
                    this.taskSmallType.push({ label: item.taskTypeName, value: item.taskTypeId })
                });
            });
        } else {
            this.taskSmallType = [{ label: '请选择', value: '' },];
        }
    }
    //事项大类
    itemsBig() {
        let param = {
            taskTypeKey: 'itemsType'
        }
        this.httpService.taskTypeQry(param).subscribe(data => {
            let arr = data.taskTypeList;
            this.itemsBigType = [{ label: '请选择', value: '' }, { label: '查询', value: '04' },];
            arr.forEach(item => {
                this.itemsBigType.push({ label: item.taskTypeName, value: item.taskTypeId });
            });
        })
    }
    //事项大类切换
    itemsBigChange() {
        if (this.taskAddBean.itemsTypeParentId && this.taskAddBean.itemsTypeParentId != '') {
            let params = {
                taskTypeParentId: this.taskAddBean.itemsTypeParentId,
                taskTypeKey: 'itemsType'
            }
            this.httpService.taskTypeQry(params).subscribe(data => {
                let arr = data.taskTypeList;
                this.itemsSmallType = [{ label: '请选择', value: '' },];
                arr.forEach(item => {
                    this.itemsSmallType.push({ label: item.taskTypeName, value: item.taskTypeId })
                });
            })
        } else {
            this.itemsSmallType = [{ label: '请选择', value: '' },];
        }
    }
    showNumAdd() {
        if (this.taskAddBean.orderNo.length > 2) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: 'Error Message', detail: '最多输入两位' });
            return;
        }
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    taskAddSave() {
        this.httpService.taskAdd(this.taskAddBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: 'Success Message', detail: '新增成功' });
                this.outValue.emit('新增成功');
                this.taskAddBean = new TaskAddBean();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        })
    }
    addCancel() {
        this.outValue.emit(false);
    }
}