import { Component, OnInit } from '@angular/core';
import { QueryListTaskTypeBean } from './bean/QueryListTaskType.bean';
import { UpdateTaskTypeBean } from './bean/UpdateTaskType.bean';
import { InsertTaskTypeBean } from './bean/insertTaskType.bean';
import { BatchDeleteTaskTypeBean } from './bean/batchDeleteTaskType.bean';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { ConfirmDialogModule, ConfirmationService, Message, SelectItem } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
@Component({
    selector: 'workType',
    templateUrl: './workType.component.html',
    styleUrls: ['./workType.component.scss'],
    providers: [ConfirmationService, CusOperationHttpService, Commfunc]
})
export class WorkTypeComponent implements OnInit {
    first: number = 0;
    userform: FormGroup;
    //总页数
    total: string;
    //每页多少条
    pageSize: string;
    chooseType: any = [
        { label: '请选择', value: '' }, { label: '是', value: '0' }, { label: '否', value: '1' }
    ];
    isStatus: any = [];
    applymgs: any = [
        { label: '请选择', value: '' }, { label: '申请', value: '1' }, { label: '审批', value: '2' }
    ];
    typeYES: any;
    typeParent: boolean = false;
    tabMesg: any = tableMessage;
    typedata: any;
    addHeader: any;
    updateHeader: any;
    typeAddPage: boolean;
    typeUpdatePage: boolean;
    msgs: Message[] = [];
    queryListTaskTypeBean: QueryListTaskTypeBean = new QueryListTaskTypeBean();
    updateTaskTypeBean: UpdateTaskTypeBean = new UpdateTaskTypeBean();
    insertTaskTypeBean: InsertTaskTypeBean = new InsertTaskTypeBean();
    batchDeleteTaskTypeBean: BatchDeleteTaskTypeBean = new BatchDeleteTaskTypeBean();
    constructor(
        public confirmationService: ConfirmationService,
        public httpService: CusOperationHttpService,
        public fb: FormBuilder,
        public commfunc: Commfunc
    ) {
        this.userform = fb.group({
            taskTypeKey: ['', Validators.required],
            taskTypeName: ['', Validators.required]
        })
    }
    ngOnInit() {
        this.queryListTaskTypeBean.pageSize = 10;
        this.queryListTaskTypeBean.pageNum = 1;
        this.updateHeader = "修改";
        this.addHeader = "新增";
        this.typedata = [];
    }
    checkThis() {
        if (this.typeYES == '0') {
            this.typeParent = true;
        } else {
            this.typeParent = false;
        }
    }
    //翻页
    paginate(event) {
        //每页显示的条数
        this.queryListTaskTypeBean.pageSize = event.rows * 1;
        //当前页
        this.queryListTaskTypeBean.pageNum = event.page + 1;
        this.first = event.page * this.queryListTaskTypeBean.pageSize;
        this.querytask();
    }
    queryFirst() {
        this.queryListTaskTypeBean.pageSize = 10;
        this.queryListTaskTypeBean.pageNum = 1;
        this.first = 0;
        this.querytask();
    }
    //工作类型查询
    querytask() {
        this.httpService.queryListTaskType(this.queryListTaskTypeBean).subscribe(data => {
            this.typedata = data.taskTypeList;
            this.total = data.totalNum;
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        })
    }
    typeAdd() {
        this.typeAddPage = true;

    }
    typeUpdate(item) {

        this.typeUpdatePage = true;
        this.updateTaskTypeBean = item;
        this.checkThis();
    }
    //工作类型删除
    typeDelete(item) {
        this.confirmationService.confirm({
            message: '是否确认删除?',
            header: '删除',
            icon: 'fa fa-trash',
            accept: () => {
                let arr = [];
                arr.push(item);
                this.batchDeleteTaskTypeBean.taskTypeList = arr;
                this.httpService.batchDeleteTaskType(this.batchDeleteTaskTypeBean).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: 'Success Message', detail: '删除成功' });
                        this.querytask();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
                })
            },
            reject: () => {
                this.msgs = [{ severity: 'info', summary: '取消删除', detail: '已取消删除' }];
            }
        });
    }
    //工作类型维护新增保存
    addSave() {
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        this.httpService.insertTaskType(this.insertTaskTypeBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: 'Success Message', detail: '保存成功' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        })
        this.typeAddPage = false;
        this.insertTaskTypeBean.taskTypeName = '';
        this.insertTaskTypeBean.taskTypeKey = '';
        this.insertTaskTypeBean.taskTypeParentId = '';
    }
    addCancel() {
        this.typeAddPage = false;
        this.insertTaskTypeBean.taskTypeName = '';
        this.insertTaskTypeBean.taskTypeKey = '';
        this.insertTaskTypeBean.taskTypeParentId = '';
    }
    //工作类型维护修改
    updateSave() {
        // this.updateTaskTypeBean = this.updateMMM;
        this.httpService.updateTaskType(this.updateTaskTypeBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: 'Success Message', detail: '修改成功' });
                this.querytask();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        })
        this.typeUpdatePage = false;
    }
    updateCancel() {
        this.typeUpdatePage = false;
    }
    myFunction() {
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        let item = {
            taskTypeKey: this.insertTaskTypeBean.taskTypeKey
        }
        this.httpService.taskTypeQry(item).subscribe(data => {
            let arr = data.taskTypeList;
            this.isStatus = [];
            this.isStatus.push({ label: '请选择', value: '' });
            arr.forEach(item => {
                this.isStatus.push({ label: item.taskTypeName, value: item.taskTypeId })
            });
        })
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }

}