import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import {WorkTaskComponent} from './workTask/workTask.component';
import {WorkTypeComponent} from './workType/workType.component';
import {WorkTaskAddComponent} from './workTask/workTaskAdd/workTaskAdd.component';

export const routes: Routes = [
    {
        path: '',
        children: [
            {
                path: 'workTask',
                component:WorkTaskComponent ,
                children:[
                    {
                        path:'workTaskAdd',
                        component:WorkTaskAddComponent
                    }
                ]
            },
            {
                path:'workType',
                component:WorkTypeComponent
            }
            
        ]
    }
]
export const OtherThingRouting: ModuleWithProviders = RouterModule.forChild(routes);