import { Component, OnInit } from '@angular/core';
import { TaskModifyBean } from './bean/taskModify.bean';
import { TaskAddBean } from './bean/taskAdd.bean';
import { TaskCategoryListQryBean } from './bean/taskCategoryListQry.bean';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { ConfirmDialogModule, ConfirmationService, Message, SelectItem } from 'primeng/primeng';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router } from "@angular/router";
@Component({
    selector: 'workTask',
    templateUrl: './workTask.component.html',
    styleUrls: ['./workTask.component.scss'],
    providers: [ConfirmationService, CusOperationHttpService, Commfunc]
})
export class WorkTaskComponent implements OnInit {
    code: any;
    statusOptions: any;//工作任务状态
    yesOrNoItems: any;//是否
    itemsTypeOptions: any;//事项大小类
    workTaskTypeOptions: any;//任务大小类
    first: number = 0;//分页控制
    //总页数
    totalNum: string;
    //每页多少条
    pageSize: string;
    msgs: Message[] = [];
    taskdata: any;
    applymgs: any = [
        { label: '请选择', value: '' }, { label: '是', value: '0' }, { label: '否', value: '1' },
    ];
    statusIdQry: any = [
        { label: '请选择', value: '' }, { label: '正常', value: '0' }, { label: '停用', value: '1' },
    ];
    subSysIdQry: any = [
        // { label: '请选择', value: '' },
        // { label: '客户运营平台', value: 'COP' },
        // { label: '统一计价平台', value: 'UVP' },
        // { label: '产品运营平台', value: 'POP' },
        // { label: '客户管理系统', value: 'CRM' },
    ]
    applymgs1: any;
    applymgs2: any;
    RelationTaskList: any = [];
    relationshow: boolean = false;
    flow: boolean = false;
    addHeader: any;
    updateHeader: any;
    workTaskaddPage: boolean = false;
    workTaskUpdatePage: boolean = false;

    selfTaskN: boolean = false;
    tabMesg: any = tableMessage;
    selfTaskY: boolean = false;
    taskcodeshow: boolean = false;
    taskBigTypeQry: any = [];//查询任务大类
    taskSmallTypeQry: any = [];//查询任务小类
    itemsBigTypeQry: any = [];//查询事项大类
    itemsSmallTypeQry: any = [];//查询事项小类

    taskModifyBean: TaskModifyBean = new TaskModifyBean();
    taskAddBean: TaskAddBean = new TaskAddBean();
    taskCategoryListQryBean: TaskCategoryListQryBean = new TaskCategoryListQryBean();
    inValueAdd: any;
    inValueUpdate: any;
    constructor(
        public confirmationService: ConfirmationService,
        public httpService: CusOperationHttpService,
        public commfunc: Commfunc,
        public router: Router,
    ) {
        this.taskBig();
        this.itemsBig();
    }
    ngOnInit() {
        this.codeValues();
        this.statusOptions = this.code['StatusWorkTask'];
        this.yesOrNoItems = this.code['YesOrNo'];
        this.itemsTypeOptions = this.code['ItemsType'];
        this.workTaskTypeOptions = this.code['WorkTaskBigType'];
        this.taskCategoryListQryBean.pageSize = 10;
        this.taskCategoryListQryBean.pageNum = 1;
        this.updateHeader = "修改";
        this.addHeader = "新增";
        this.taskdata = [];
    }
    //翻页
    paginate(event) {
        //每页显示的条数
        this.taskCategoryListQryBean.pageSize = event.rows * 1;
        //当前页
        this.taskCategoryListQryBean.pageNum = event.page + 1;
        this.first = event.page * this.taskCategoryListQryBean.pageSize;
        this.taskQuery();
    }
    //任务大类
    taskBig() {
        let item = {
            taskTypeKey: 'taskType'
        }
        this.httpService.taskTypeQry(item).subscribe(data => {
            let arr = data.taskTypeList;
            this.taskBigTypeQry = [{ label: '请选择', value: '' },];
            arr.forEach(param => {
                this.taskBigTypeQry.push({ label: param.taskTypeName, value: param.taskTypeId });
            });
        })
    }
    //任务大类切换
    taskBigChange() {
        if (this.taskCategoryListQryBean.taskTypeParentId && this.taskCategoryListQryBean.taskTypeParentId != '') {
            let params = {
                taskTypeParentId: this.taskCategoryListQryBean.taskTypeParentId,
                taskTypeKey: 'taskType'
            }
            this.httpService.taskTypeQry(params).subscribe(data => {
                let arr = data.taskTypeList;
                this.taskSmallTypeQry = [{ label: '请选择', value: '' },];
                arr.forEach(item => {
                    this.taskSmallTypeQry.push({ label: item.taskTypeName, value: item.taskTypeId })
                });
            });
        } else {
            this.taskSmallTypeQry = [{ label: '请选择', value: '' },];
        }
    }
    //事项大类
    itemsBig() {
        let param = {
            taskTypeKey: 'itemsType'
        }
        this.httpService.taskTypeQry(param).subscribe(data => {
            let arr = data.taskTypeList;
            this.itemsBigTypeQry = [{ label: '请选择', value: '' }, { label: '查询', value: '04' },];
            arr.forEach(item => {
                this.itemsBigTypeQry.push({ label: item.taskTypeName, value: item.taskTypeId });
            });
        })
    }
    //事项大类切换
    itemsBigChange() {
        if (this.taskCategoryListQryBean.itemsTypeParentId && this.taskCategoryListQryBean.itemsTypeParentId != '') {
            let params = {
                taskTypeParentId: this.taskCategoryListQryBean.itemsTypeParentId,
                taskTypeKey: 'itemsType'
            }
            this.httpService.taskTypeQry(params).subscribe(data => {
                let arr = data.taskTypeList;
                this.itemsSmallTypeQry = [{ label: '请选择', value: '' },];
                arr.forEach(item => {
                    this.itemsSmallTypeQry.push({ label: item.taskTypeName, value: item.taskTypeId })
                });
            })
        } else {
            this.itemsSmallTypeQry = [{ label: '请选择', value: '' },];
        }
    }
    showNumAdd() {
        if (this.taskAddBean.orderNo.length > 2) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: 'Error Message', detail: '最多输入两位' });
            return;
        }
    }
    showNumUpdate() {
        if (this.taskModifyBean.orderNo.length > 2) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: 'Error Message', detail: '最多输入两位' });
            return;
        }
    }
    queryFirst() {
        this.taskCategoryListQryBean.pageSize = 10;
        this.taskCategoryListQryBean.pageNum = 1;
        this.first = 0;
        this.taskQuery();
    }
    //查询
    taskQuery() {
        this.httpService.taskCategoryQry(this.taskCategoryListQryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.taskdata = data.taskCategoryList;
                this.totalNum = data.total;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    workTaskAdd() {
        this.workTaskaddPage = true;
    }
    workTaskUpdate(item) {
        this.workTaskUpdatePage = true;
        this.inValueUpdate = item;
    }
    workTaskDelete(item) {
        this.confirmationService.confirm({
            message: '是否确认删除?',
            header: '删除',
            icon: 'fa fa-trash',
            accept: () => {
                let param = { taskCategoryId: item.taskCategoryId };
                this.httpService.taskDel(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: 'Success Message', detail: '删除成功' });
                        this.taskQuery();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
                })
            },
            reject: () => {
                this.msgs = [];
                this.msgs = [{ severity: 'info', summary: '取消删除', detail: '已取消删除' }];
            }
        });
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    addCall(event) {
        if (event == '新增成功') {
            this.queryFirst();
            this.workTaskaddPage = false;
        } else {
            this.workTaskaddPage = false;
        }
    }
    updateCall(event) {
        if (event == '修改成功') {
            this.queryFirst();
            this.workTaskUpdatePage = false;
        } else {
            this.workTaskUpdatePage = false;
        }
    }

}